from '@/api/user'


export default {
data() {
return {
form: {
name: '',
tel: '',
address: '',
city: []
},
addressData: ChinaAddressV4Data,
showAddress: false,
toastText: '', // 弹出框
toast: false
}
},
components: {
Group,
XInput,
XButton,
Divider,
Flexbox,
FlexboxItem,
Toast,
Cell,
XAddress
},
computed: {
...mapGetters([
'username',
'telphone',
